import React from 'react';
import { 
  Users, 
  Building2, 
  Award, 
  Activity,
  TrendingUp,
  TrendingDown,
  BarChart3
} from 'lucide-react';

const Dashboard = () => {
  const stats = [
    {
      title: 'Total Organizations',
      value: '12',
      change: '+2.5%',
      trend: 'up',
      icon: Building2,
      color: 'blue'
    },
    {
      title: 'Total Users',
      value: '1,234',
      change: '+12.3%',
      trend: 'up',
      icon: Users,
      color: 'green'
    },
    {
      title: 'Active Certificates',
      value: '89',
      change: '-2.1%',
      trend: 'down',
      icon: Award,
      color: 'yellow'
    },
    {
      title: 'System Activity',
      value: '98.5%',
      change: '+0.8%',
      trend: 'up',
      icon: Activity,
      color: 'purple'
    }
  ];

  const getColorClasses = (color) => {
    const colors = {
      blue: 'bg-blue-100 text-blue-600',
      green: 'bg-green-100 text-green-600',
      yellow: 'bg-yellow-100 text-yellow-600',
      purple: 'bg-purple-100 text-purple-600'
    };
    return colors[color] || colors.blue;
  };

  return (
    <div className="space-y-6">
      
      {/* Page Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
        <p className="text-gray-600">Welcome back! Here's what's happening with your organization.</p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {stats.map((stat, index) => {
          const Icon = stat.icon;
          const TrendIcon = stat.trend === 'up' ? TrendingUp : TrendingDown;
          
          return (
            <div key={index} className="bg-white rounded-lg border border-gray-200 p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">{stat.title}</p>
                  <p className="text-2xl font-bold text-gray-900 mt-2">{stat.value}</p>
                </div>
                <div className={`w-12 h-12 rounded-lg flex items-center justify-center ${getColorClasses(stat.color)}`}>
                  <Icon size={24} />
                </div>
              </div>
              <div className="mt-4 flex items-center">
                <TrendIcon 
                  size={16} 
                  className={stat.trend === 'up' ? 'text-green-500' : 'text-red-500'} 
                />
                <span className={`text-sm font-medium ml-1 ${
                  stat.trend === 'up' ? 'text-green-600' : 'text-red-600'
                }`}>
                  {stat.change}
                </span>
                <span className="text-sm text-gray-500 ml-2">from last month</span>
              </div>
            </div>
          );
        })}
      </div>

      {/* Charts Section */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        
        {/* Activity Chart */}
        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">Activity Overview</h3>
            <BarChart3 size={20} className="text-gray-400" />
          </div>
          <div className="h-64 flex items-center justify-center bg-gray-50 rounded-lg">
            <p className="text-gray-500">Chart will be implemented here</p>
          </div>
        </div>

        {/* Recent Activity */}
        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Recent Activity</h3>
          <div className="space-y-4">
            {[
              { action: 'New user registered', user: 'John Doe', time: '2 minutes ago' },
              { action: 'Organization updated', user: 'Jane Smith', time: '1 hour ago' },
              { action: 'Certificate issued', user: 'Mike Johnson', time: '3 hours ago' },
              { action: 'System backup completed', user: 'System', time: '6 hours ago' },
            ].map((activity, index) => (
              <div key={index} className="flex items-center justify-between py-2">
                <div>
                  <p className="text-sm font-medium text-gray-900">{activity.action}</p>
                  <p className="text-xs text-gray-500">by {activity.user}</p>
                </div>
                <span className="text-xs text-gray-400">{activity.time}</span>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="bg-white rounded-lg border border-gray-200 p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <button className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors text-left">
            <Building2 size={24} className="text-blue-600 mb-2" />
            <h4 className="font-medium text-gray-900">Add Organization</h4>
            <p className="text-sm text-gray-500">Create a new organization</p>
          </button>
          <button className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors text-left">
            <Users size={24} className="text-green-600 mb-2" />
            <h4 className="font-medium text-gray-900">Manage Users</h4>
            <p className="text-sm text-gray-500">Add or edit user accounts</p>
          </button>
          <button className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors text-left">
            <Award size={24} className="text-yellow-600 mb-2" />
            <h4 className="font-medium text-gray-900">Issue Certificate</h4>
            <p className="text-sm text-gray-500">Generate new certificates</p>
          </button>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
